







































































































import { Component, Vue } from 'vue-property-decorator';
import { Getter, namespace } from 'vuex-class';
import { UserInfo } from '@/types';
import UserService from '@/services/userService';
const Auth = namespace('Auth');
@Component({
  components: {

  }
})
export default class TopStories extends Vue {
  @Getter('userData', { namespace: 'Auth' }) userData!: UserInfo;
  @Auth.Action
  private signOut!: () => Promise<any>;

  loadingOverlay = false;

  async mounted () {
    if (this.userData && this.userData.autoResume) {
      this.settings.push(0);
    }
    if (this.userData && this.userData.guaranteedProfit) {
      this.settings.push(1);
    }
  }

  logOut (username: string, password: string) {
    this.signOut().then(
      (data) => {
        window.location.href = '';
      }
    );
  }

  get settings () {
    const setting = [];
    if (this.userData && this.userData.autoResume) {
      setting.push(0);
    }
    if (this.userData && this.userData.guaranteedProfit) {
      setting.push(1);
    }
    if (this.userData && this.userData.pauseBot) {
      setting.push(2);
    }
    return setting;
  }

  set settings (val) {
    if (this.userData.autoResume !== val.includes(0)) {
      this.loadingOverlay = true;
      UserService.changeAutoResume(val.includes(0)).then((data) => {
        if (data.success) {
          this.userData.autoResume = val.includes(0);
        }
        this.loadingOverlay = false;
      });
    }
    if (this.userData.guaranteedProfit !== val.includes(1)) {
      this.loadingOverlay = true;
      UserService.changeGuaranteedProfit(val.includes(1)).then((data) => {
        if (data.success) {
          this.userData.guaranteedProfit = val.includes(1);
        }
        this.loadingOverlay = false;
      });
    }
    if (this.userData.pauseBot !== val.includes(2)) {
      this.loadingOverlay = true;
      UserService.pauseBot(val.includes(2)).then((data) => {
        if (data.success) {
          this.userData.pauseBot = val.includes(2);
        }
        this.loadingOverlay = false;
      });
    }
  }
}
